 problemas se vuelvan críticos")])]),_c('p',{attrs:{"data-aos":"fade-down"}},[_c('b',[_vm._v("Ejemplo.")]),_vm._v(" En un proyecto de desarrollo de software, el KPI de tasa de defectos ha estado consistentemente por encima del objetivo del 0.5 defectos por cada 1000 líneas de código. El gerente del proyecto investiga y descubre que la causa principal es una falta de pruebas automatizadas integrales. Como acción correctiva, asignan un desarrollador dedicado para implementar pruebas automatizadas para las funcionalidades claves. También organizan una sesión de capacitación para todo el equipo, sobre las mejores prácticas de pruebas y hacen de las pruebas una parte integral del proceso de desarrollo. Con el tiempo, estas acciones correctivas resultan en una disminución de la tasa de defectos y ayudan al proyecto, a cumplir con el objetivo.")])]),_c('div',{staticClass:"col-lg-4"},[_c('figure',[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/48.png"),"data-aos":"zoom-in"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo",attrs:{"id":"t_3_5","data-aos":"flip-up"}},[_c('h2',[_c('span',[_vm._v("3.5")]),_vm._v(" Análisis y evaluación continua del rendimiento")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-8 mb-3 mb-lg-0"},[_c('div',{staticClass:"bg-color-2 p-4 j1 mb-4",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Sarmiento Rojas et al. (2020), sugieren usar el análisis de tendencias para identificar patrones en el rendimiento a lo largo del tiempo. Esto implica comparar los KPI actuales con los valores históricos para ver si el rendimiento está mejorando, se mantiene estable o se deteriora. Esta información puede ayudar a los gerentes de proyectos a tomar decisiones informadas sobre asignación de recursos, capacitación del equipo o adopción de nuevas metodologías.")])]),_c('p',{attrs:{"data-aos":"fade-down"}},[_c('b',[_vm._v("Ejemplo.")]),_vm._v(" En un proyecto de desarrollo de software, el equipo realiza una revisión trimestral del rendimiento. Analizan los datos de KPI de los últimos tres meses, incluyendo la velocidad del equipo, la tasa de defectos y la satisfacción del cliente. Notan una tendencia positiva en la velocidad del equipo, pero observan que la satisfacción del cliente ha disminuido ligeramente. Durante la revisión, el equipo discute posibles razones para la disminución de la satisfacción del cliente y propone acciones para mejorar, como una mayor participación del cliente en las revisiones de sprint y una comunicación más frecuente sobre el progreso. Estas ideas se incorporan al plan del proyecto para el próximo trimestre, con KPI actualizados para medir su efectividad.")])]),_c('div',{staticClass:"col-lg-4"},[_c('figure',[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/51.png"),"data-aos":"zoom-in"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('figure',[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/52.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8"},[_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("A lo largo de esta unidad, se han explorado los aspectos fundamentales de la planificación y control de proyectos de software. Desde las técnicas de planificación hasta el uso de diagramas de Gantt y el seguimiento de KPIs, cada tema abordado contribuye a una gestión efectiva de los proyectos.")]),_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("Comprender las diferencias entre los enfoques ágiles y tradicionales, permite adaptar la estrategia de planificación, según las necesidades específicas del proyecto.")]),_c('div',{staticClass:"bg-color-1 p-4 j1 mb-4",attrs:{"data-aos":"fade-left"}},[_c('p',[_vm._v("Las técnicas de estimación de tiempos y costos ayudan a establecer expectativas realistas y asignar recursos de manera efectiva. El desarrollo de una estructura de desglose del trabajo (WBS) proporciona una hoja de ruta clara para el equipo y facilita el seguimiento del progreso.")]),_c('p',[_vm._v("Los diagramas de Gantt han demostrado ser una herramienta valiosa para visualizar el cronograma del proyecto, gestionar dependencias y comunicar hitos claves. Su uso fomenta la transparencia y la colaboración dentro del equipo.")]),_c('p',{staticClass:"mb-0"},[_vm._v("El seguimiento de los Indicadores Claves de Desempeño (KPI) es esencial para medir el éxito del proyecto y tomar decisiones basadas en datos. Al establecer KPIs relevantes, monitorearlos regularmente y tomar acciones correctivas cuando sea necesario, se puede mantener el proyecto en camino y entregar resultados de alta calidad.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-8 mb-3 mb-lg-0"},[_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("Sin embargo, es importante recordar que la planificación y el control de proyectos no se tratan solo de herramientas y técnicas. También requieren habilidades interpersonales, como la comunicación efectiva, el liderazgo y la resolución de problemas. Fomentar una cultura de mejora continua y aprendizaje dentro del equipo, es igual de crucial para el éxito del proyecto.")]),_c('div',{staticClass:"bg-color-2 p-4 j1 mb-4",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Como futuros ingenieros de software, dominar estas habilidades de gestión de proyectos los preparará para los desafíos del mundo real. Permitirá no solo entregar software de alta calidad a tiempo y dentro del presupuesto, sino también innovar, adaptarse al cambio y crear valor para las organizaciones y clientes.")])]),_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("En última instancia, la planificación y el control efectivos de proyectos, son fundamentales para el éxito de cualquier iniciativa de desarrollo de software. Al aplicar los conceptos y prácticas discutidos en esta unidad, estarán bien preparados para enfrentar la complejidad inherente a los proyectos de software y liderar equipos hacia el logro de sus objetivos.")])]),_c('div',{staticClass:"col-lg-4"},[_c('figure',[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/53.png"),"data-aos":"zoom-in"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-12 col-md-6 col-lg-3 offset-lg-1"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/componentes/material-complementario.svg"),"alt":""}})])])}]

